.Home {
  padding: 0 75px;
  padding-bottom: 50px;
  background-repeat: no-repeat;
  max-width: 100vw;
  overflow: hidden;
  background-size: 100%;
}
.home-header {
  padding-top: 170px;
  display: flex;
  font-family: 'Nunito', sans-serif;
  font-size: 50px;
  font-weight: 300;
}

.btn-display {
  display: flex;
  justify-content: space-between;
}

.rec-btn {
  margin-top: 40px;
  border: none;
  cursor: pointer;
  background-color: #65AA91;
  font-size: 20px;
  padding: 8px;
  width: 200px;
  border-radius: 10px;
  font-family: 'Lato', sans-serif;
}

.sortby-btn {
  margin-top: 40px;
  border: none;
  cursor: pointer;
  background-color: #65AA91;
  font-size: 20px;
  padding: 8px;
  width: 110px;
  border-radius: 10px;
  font-family: 'Lato', sans-serif;
}

.rec-btn:hover, 
.sortby-btn:hover, 
.rec-btn:active, 
.sortby-btn:active {
  background-color: #79b49f;
  color: #000;
}

.active-sort {
  background-color: #d8d79c !important;
}

.sortby-dropdown button:hover,
.sortby-dropdown button:active {
  background-color: #d8d7a2;
  color: #000;
}

.sortby-dropdown {
  background-color: #70b99f;
  position: absolute;
  display: flex;
  flex-direction: column;
  z-index: 1;
  width: 110px;
  border-radius: 10px;
  overflow: hidden;
}
.sortby-dropdown button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  font-size: 18px;
  margin: 0;
  padding: 5px;
  font-family: 'Lato', sans-serif;
}

.category-header {
  font-size: 25px;
  font-family: 'Baloo 2', sans-serif;
}

@media only screen and (max-width: 700px) {
