* {
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
}

textarea {
  font-family: var(--font-family);
  border: none;
  resize: none;
}

textarea::placeholder {
  color: #aaa;
}

label {
  height: 0;
  width: 0;
  visibility: hidden;
}

button {
  cursor: pointer;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

button[type="submit"] {
  background: #6210f5;
  color: #fff;
  border-radius: 5px;
}

