  border: none;
}

.container {
  padding: 20px;
}

.comment-box {
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  padding: 25px;
  transition: min-height 0.4s ease;
  max-height: 75px;
}

.header {
  transition: opacity 0.4s ease 0.2s;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-40px);
  padding: 20px;
}

.user {
  display: flex;
  align-items: center;
}

.user img {
  border-radius: 20px;
  margin-right: 10px;
}

.comment-field {
  transition: transform 0.4s ease;
  font-size: 18px;
  width: 100%;
  outline: none;
  padding: 0;
  min-height: 60px;
  transform: translateY(-32px);
  line-height: 1;
  background-color: white;
}

.comment-box.expanded {
  border: 1px solid black;
}

.comment-box.expanded .comment-field {
  transform: translateY(40px);
}

.comment-box.expanded .header {
  transform: translateY(10px);
  opacity: 1;
  visibility: visible;
}

.actions {
  width: 100%;
  display: flex;
  align-self: flex-end;
  justify-content: flex-end;
  margin-top: 50px;
  opacity: 0;
  transition: opacity 0.4s ease 0.2s;
}

.comment-box.expanded .actions {
  opacity: 1;
}

.actions button {
  margin-left: 6px;
  font-size: 14px;
  padding: 12px;
  border: none;
  line-height: 1;
}

.actions .cancel {
  background: none;
}

.comment-box.modified {
  max-height: unset;
}

.user_info {
  /*border: 1px solid black;*/
  margin-left: 30px;
}

